import './App.scss';
import react from 'react';
import logo from './img/logo.png';
import intro_bg from './img/intro-bg.png';
import list_img_1 from './img/list-1.png';
import list_img_2 from './img/list-2.png';
import list_img_3 from './img/list-3.png';
import facebook from './img/facebook.png';
import twiter from './img/twitter.png'
import instagram from './img/instagram.png'
import language from './component/localizeshion';
import { useState } from 'react'
import { useRef } from 'react'
function App() {
  const [lang, setlang] = useState(`en`)
  const sun = useRef()
  const moon = useRef()
  const [theme, settheme] = useState(`light`)
  let body = document.querySelector(`body`)
  let theme4 = window.localStorage.getItem(`theme3`)
  let lang3 = window.localStorage.getItem(`setlang2`)
  console.log(lang3)
  window.onload = () => {
    if(theme4 == `light`){
      settheme(`dark`)
      moon.current.classList.add(`hide`)
      sun.current.classList.remove(`hide`)
      body.classList.remove(`theme-dark`)
    }
    else if(theme4 == `dark`){
      settheme(`light`)
      moon.current.classList.remove(`hide`)
      sun.current.classList.add(`hide`)
      body.classList.add(`theme-dark`)
    }
    if(lang3.length > 0){
      setlang(lang3)
    }
  }
  
  return (
   <>
   <header>
     <nav>
       <img src={logo}  alt="hato"/> 
       <select onChange={(e) => {
         setlang(e.target.value)
         console.log(`salom`)
         window.localStorage.setItem(`setlang2`, e.target.value)
       }}>
         <option value="uz">uz</option>
         <option value="ru">ru</option>
         <option value="en">en</option>
       </select>
        <button onClick={() => {
          if(theme == `light`){
            settheme(`dark`)
            let theme2 = window.localStorage.setItem(`theme3`, theme)
            moon.current.classList.add(`hide`)
            sun.current.classList.remove(`hide`)
           
            body.classList.remove(`theme-dark`)
          }
          else if(theme == `dark`){
            settheme(`light`)
            let theme2 = window.localStorage.setItem(`theme3`, theme)
            moon.current.classList.remove(`hide`)
            sun.current.classList.add(`hide`)
            body.classList.add(`theme-dark`)
          }
        }}>
            <i ref={moon} class='bx bx-moon hide'></i>
            <i ref={sun} class='bx bx-sun ' ></i>
        </button>
       <ul> 
         <li>
           <a href="#">{language[`${lang}`].text1}</a>
         </li>
         <li>
           <a href="#">{language[`${lang}`].text2}</a>
         </li>
         <li>
           <a href="#">{language[`${lang}`].text3}</a>
         </li>
         <li>
           <a href="#">{language[`${lang}`].text4}</a>
         </li>
       </ul>
     </nav>
   </header>
   <main>
     <section className="intro">
        <div>
          <div className="intro-wrapper">
            <h4>{language[`${lang}`].text5}</h4>
            <h1>{language[`${lang}`].text6}</h1>
            <p>{language[`${lang}`].text7}</p>
            <a href="#">Playstore</a>
            <a className="link-1" href="#">App store</a>

            <img src={intro_bg} />
          </div>
        </div>
     </section>
     <section>
       <div className="container">
          <div className="list-wrapper">
            <h1>{language[`${lang}`].text8}</h1>
            <ul>
              <li>
                <img src={list_img_1} />
                <div>
                  <h4>{language[`${lang}`].text9}</h4>
                  <h2>{language[`${lang}`].text10}</h2>
                  <p>{language[`${lang}`].text11}</p>
                </div>
              </li>
              <li className="teskari">
                <img src={list_img_2} />
                <div>
                  <h4>{language[`${lang}`].text12}</h4>
                  <h2>{language[`${lang}`].text13}</h2>
                  <p>{language[`${lang}`].text14}</p>
                </div>
              </li>
              <li>
                <img src={list_img_3} />
                <div>
                  <h4>{language[`${lang}`].text15}</h4>
                  <h2>{language[`${lang}`].text16}</h2>
                  <p>{language[`${lang}`].text17}</p>
                </div>
              </li>
            </ul>
          </div>
       </div>
     </section>
     <section className="end-section">
       <div className="container">
            <div className="end">
              <h1>{language[`${lang}`].text18}</h1>
              <p>{language[`${lang}`].text19}</p>
              <a href="#">Playstore</a>
              <a href="#" className="link-2">App store</a>
            </div>
       </div>
     </section>
   </main>
   <footer>
     <div className="container">
        <div className="footer">
            <img src={logo} />
            <ul>
              <li><img src={twiter} /></li>
              <li><img src={facebook} /></li>
              <li><img src={instagram} /></li>
            </ul>
            <a href="#">Copywright 2020 Bella Onojie.com</a>
        </div>
     </div>
   </footer>
   </>
  );
}

export default App;
